<template>
  <div>
    <div class="d-flex justify-center mb-6 mt-6">
      <v-img
        src="@/assets/logoInfo.png"
        max-height="150"
        max-width="250"
      ></v-img>
    </div>
    <v-container>
      <Botones/>
      <v-form ref="form" @submit.prevent="submit">
        <v-autocomplete  label="Seleccione el cliente" v-model="cliente.cliente" :items="clientes" required :rules="clienteRules"></v-autocomplete>
        <v-menu
          v-model="menu2"
          :close-on-content-click="false"
          full-width
          max-width="290"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              :value="computedDateFormattedDatefns"
              clearable
              label="Seleccione la fecha"
              readonly
              v-on="on"
              v-model="cliente.date"
              :rules="fechaRules"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="cliente.date"
            @change="menu2 = false"
          ></v-date-picker>
        </v-menu>
        <v-autocomplete label="Seleccione el producto" :items="productos" v-model="cliente.productos" :rules="productosRules"></v-autocomplete>
        <v-text-field type="Number" label="Seleccione la cantidad" v-model="cliente.cantidades" :rules="cantidadRules"></v-text-field>
        <v-btn color="greeen" @click="enviar">
          Enviar
        </v-btn>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import { format } from 'date-fns'
import axios from 'axios'
import Botones from '@/components/Botones'
export default {
  components:{
    Botones
  },
  data() {
    return {
      date: new Date().toISOString().slice(0, 10),
      valid: true,
      clienteRules: [
        v => !!v || 'Selecciona un cliente',
      ],
      fechaRules: [
        v => !!v || 'Selecciona una fecha',
      ],
      productosRules: [
        v => !!v || 'Selecciona un producto',
      ],
      cantidadRules: [
        v => !!v || 'Selecciona un producto',
      ],
      menu2: false,
      clientes:[],
      productos:[],
      cliente: {
        cliente: "",
        date: "",
        productos: "",
        cantidades: ""
      },
    }
  },
  created(){
    this.mostrarClientesInfo()
    this.mostrarProductosInfo()
  },
  methods:{
    enviar(){
      axios.post(this.$url + '/cliente', this.cliente, {
          headers: {
            "Content-Type": "application/json",
          },
        })
      .then((result) =>{
        console.log(result)
        this.cliente= {
          cliente: "",
          date: "",
          productos: "",
          cantidades: ""
        }
      }).catch((error) =>{
        console.log(error)
      })
    },
    mostrarClientesInfo(){  
      axios.post('https://www.infofast.com.mx/Erick/service/clientes/',
        {
          user: 'user_pruebas',
          pwd: 'Pru3B@5.'
        },{
          headers:{
            "Content-Type": "application/x-www-form-urlencoded"
          },
        }
      ).then((result) =>{
        result.data.forEach(element => {
          // console.log('es este', element)
          let dato = element.nombre
          this.clientes.push(dato) 
        })
      }).catch((error) =>{
        console.log(error)
      })
    },
    mostrarProductosInfo(){  
      axios.post('https://www.infofast.com.mx/Erick/service/productos/',
        {
          user: 'user_pruebas',
          pwd: 'Pru3B@5.'
        },{
          headers:{
            "Content-Type": "application/x-www-form-urlencoded"
          },
        }
      ).then((result) =>{
        result.data.forEach(element => {
          // console.log('es este', element)
          let dato = element.nombre
          let dato2 = element.Precio
          this.productos.push(dato + ' $' + dato2) 
        })
      }).catch((error) =>{
        console.log(error)
      })
    }
  },
  computed:{
    computedDateFormattedDatefns () {
      return this.date ? format(new Date(this.date), 'dd-MM-yyyy') : ''
      }
  }
};
</script>